  exports['default'] = Ember['default'].Component.extend(Ember['default'].I18n.TranslateableProperties, {
    tagName: 'popover',
    attributeBindings: [ 'title', 'content:data-content' ],

    didInsertElement: function () {
      this.$().popover({
        html: true,
        placement: 'left',
        trigger: 'hover'
      });

      this.$().attr('data-content', this.$('.hide').html());
    }
  });

